@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}

body{
  background-color: #fb7375;
  background-image: url(/reactweek5/static/media/bg-intro-desktop.059183ee.png);
  background-size: cover;
}

.App{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 15%;
}

.App .box-1{
  margin-left: -20%;
  margin-right: 20%;  
  font-family:'Poppins', sans-serif;
  font-weight: 400;
  color: white;
  font:16px;
}

.App .box-1 h1{
  font-family:'Poppins', sans-serif;
  font-weight: 700;
  color: white;
}


.App .box-2{
  margin-top: -10%;
  margin-right:-22%;
}

.box-2 .free-trial{
  background-color: #5d54a3;
  padding: 1%;
  height: 45px;
  width: 100%;
  margin-bottom: 3%;
  border-radius: 15px;
}

.box-2 .free-trial p{
  font-family:'Poppins', sans-serif;
  font-weight: 400;
  color: white;
  margin-left: 15%;
  margin-top: 1%;
}


.box-2 .form-box{
  background-color: white;
  width: 470px;
  height: 415px;
  border-radius: 3%;
}

.box-2 .form-box form{
  padding-top: 7%;
  padding-left: 35%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: space-between;
          align-content: space-between;
}

.box-2 .form-box form input{
  width: 118%;
  padding: 5%;
  border-radius: 10px;
  margin-left: -38%;
  margin-bottom: 5%;
  border-color: #dcdcdc;
  outline-color: purple;
}

.box-2 .form-box .bttn button{
  width: 75%;
  padding:1%;
  background-color: #38cc8a;
  margin-left: 11%;
  border-radius: 10px;
  border-color: #51be8f;
  color: white;
  font-family:'Poppins', sans-serif;
  font-weight: 500;
  font-size: 16px;
}

.box-2 .form-box .terms{
  margin-left: 10%;
  margin-top: 20px;
  font-family:'Poppins', sans-serif;
  font-weight: 400;
  font-size: 12px;
  color:grey;
}

.box-2 .form-box .terms .red{
  margin-top: 20px;
  font-family:'Poppins', sans-serif;
  font-weight: 400;
  font-size: 12px;
  color:red;
}


@media screen and (max-width: 600px){
    
  .App {
      display: block;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
  }

  .box-1{
      width: 360px;
      margin-left: 10%;
  }
  
  .box-1 h1 {
      margin-left: 20%;
      width: 366px;
  }

  .box-1 p {
    margin-left: 20%;
    font: 16px;
  }

  .box-2 {
      margin-top: -10%;
      margin-right: -30%;
      margin-left: 4%;
  }

  .box-2 .free-trial{
      border-radius: 15px;
      width: 121%;
      height: 70px;
      margin-left: -20%;
      margin-top:16%;
      margin-bottom: 3%;
  }

  .box-2 .free-trial p{
      margin-left: -2%;
      margin-top: 20px;
      font-size: 16px;
      text-align: center;
  }

  .box-2 .form-box {
      width: 121%;
      height: 400px;
      border-radius: 3%;
      margin-left: -20%;
  }

  .box-2 .form-box form {
    padding-top: 15%;
  }

  .box2 .form-box .terms{
      margin-left: -35%;
      padding-top: 10%;
      font-family:'Poppins', sans-serif;
      font-weight: 400;
      font-size: 13px;
  }

}
